<template>
  <v-app>
    <div>
      <v-app-bar>
        <a
          href="/"
          v-if="focusedCountry != ''"
          class="text-decoration-none ml-n4"
        >
          <v-btn class="mx-2" fab dark small depressed color="primary">
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
        </a>
        <div>COVID-19 STATS</div>
        <!-- <router-link to="/" class="mr-2">Home</router-link> -->
      </v-app-bar>
    </div>

    <countryHistory
      :loadWhichCountry="focusedCountry"
      v-if="focusedCountry != ''"
      @updateRawHistory="updateRawHistory"
    ></countryHistory>
    <v-container>
      <v-form ref="form" v-if="focusedCountry == ''">
        <v-text-field
          v-model="search"
          label="Filter Country Name"
          filled
          class="flex-shrink-0"
        ></v-text-field>
      </v-form>

      <countryQuickStats
        countryURL
        :search="search"
        v-if="focusedCountry == ''"
      ></countryQuickStats>
      <v-row v-if="!areCountriesLoaded">
        <v-col class="text-center">
          <v-progress-circular
            indeterminate
            color="primary"
          ></v-progress-circular>
        </v-col>
      </v-row>
      <countryQuickStats
        :countryURL="focusedCountry"
        :search="focusedCountry"
        v-if="focusedCountry != ''"
      ></countryQuickStats>
    </v-container>
    <rawDataTable :rawHistoryData="rawHistoryData">
    </rawDataTable>
    <v-footer padless>
      <v-card class="flex" flat tile>
        <v-card-title>
          <div class="footer-msg font-italic text-xs">
            "Help prevent the spread of COVID-19. Wash your hands often,
            maintain social distancing and stay home if you feel unwell."
          </div>
        </v-card-title>
        <v-card-text>
          <div class="py-2 site-author text-center text-xxs">
            {{ new Date().getFullYear() }} â€”
            <strong>Artvader</strong>
          </div>
        </v-card-text>
      </v-card>
    </v-footer>
  </v-app>
</template>

<script>
// @ is an alias to /src
import countryHistory from "@/components/countryHistory.vue";
import countryQuickStats from "@/components/countryQuickStats.vue";
import rawDataTable from "@/components/rawDataTable.vue";
export default {
  name: "App",
  components: {
    countryHistory,
    countryQuickStats,
    rawDataTable
  },
  metaInfo: {
    meta: [
      { charset: "utf-8" },
      {
        name: "description",
        content: "A SARS-COV-2 Worldwide Statistics Web App.",
      },
      {
        itemprop: "name",
        content: "A SARS-COV-2 Worldwide Statistics Web App.",
      },
      {
        itemprop: "image",
        content: "https://www.cov19spy.com/images/thumbnail.png",
      },
      { property: "og:title", content: "COV19Spy" },
      { property: "og:type", content: "website" },
      {
        property: "og:description",
        content:
          "A SARS-COV-2 Worldwide Statistics Web App. Showing the latest information on how well each country is winning the battle again the novel Corona Virus Disease.",
      },
      {
        property: "og:image",
        content: "http://www.cov19spy.com/images/thumbnail.png",
      },
      { property: "og:url", content: "https://www.cov19spy.com" },
    ],
  },
  title() {
    return `COVID-19 Worldwide Stats ${this.focusedCountry.toUpperCase()}`;
  },
  data: () => ({
    //
    pageLocation: "",
    to: {},
    from: {},
    search: "",
    areCountriesLoaded: false,
    focusedCountry: "",
    rawHistoryData: [],
  }),
  methods: {
    pageIdentify() {
      if (localStorage.getItem("path") === null) {
        localStorage.setItem("path", "");
      } else {
        this.focusedCountry = localStorage.getItem("path");
        window.history.pushState("", "", "/" + localStorage.getItem("path"));
      }
      // return window.location.href.split("/");
    },
    updateRawHistory(e) {
      this.rawHistoryData = e;
    },
  },
  watch: {
    $route: function(to, from) {
      this.to = to;
      this.from = from;
    },
  },
  computed: {
    filteredItems() {
      return this.$store.getters.worldData.filter((item) => {
        return (
          item.country.toLowerCase().indexOf(this.search.toLowerCase()) > -1
        );
      });
    },
  },
  created() {
    this.pageIdentify();
    this.$store.watch(
      (state) => state.countryList,
      (value) => {
        if (value) {
          this.areCountriesLoaded = true;
        }
      }
    );
  },
  mounted() {
    this.pageIdentify();
  },
};
</script>

<style lang="scss">
@import "https://cdn.jsdelivr.net/npm/@mdi/font@5.x/css/materialdesignicons.min.css";
@import "./styles/02_molecules/text-size-helper";
@include dark-theme() {
  background: radial-gradient(circle at top left, #19283b 75%, #3a495c 95%);
  background-repeat: no-repeat;
  background-position: top;
  .v-app-bar {
    height: rem(64) !important;
    .v-toolbar__content {
      height: rem(64) !important;
    }
  }
  .primary-text {
    color: rgba($white, 0.5);
  }
  .v-skeleton-loader__avatar {
    width: 3.5rem;
  }
  header {
    background-image: linear-gradient(to right, #dd185a 10%, #fe6702 90%);
  }
}

.v-application .rounded {
  border-radius: rem(4) !important;
}

.footer-msg {
  line-height: 1.2em;
  opacity: 0.3;
  word-break: normal;
}

.site-author {
  opacity: 0.3;
}
</style>
