<template>
  <div v-if="rawHistoryData.length > 0">
    <v-simple-table
      class="mx-2 mb-12"
      :dark="false"
      :light="false"
      :dense="true"
    >
      <template v-slot:default>
        <thead>
          <tr>
            <th class="text-left">DATE</th>
            <th class="text-left">NEW</th>
            <th class="text-left">DEATHS</th>
            <th class="text-left">RECOVERED</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in rawHistoryData" :key="item.time">
            <td>{{ item.time | moment("MM/DD/YYYY") }}</td>
            <td class="text-center">
              {{ Number(item.cases.new).toLocaleString() }}
            </td>
            <td class="text-center">
              {{ Number(item.deaths.new).toLocaleString() }}
            </td>
            <td class="text-center">
              {{ item.cases.recovered.toLocaleString() }}
            </td>
          </tr>
        </tbody>
      </template>
    </v-simple-table>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: () => ({
    //
  }),
  props: {
    rawHistoryData: {
      type: Object,
      default: () => {},
    },
  },
};
</script>

<style lang="scss" scoped>
.theme--dark {
  &.v-data-table {
    background: transparent;
    border-radius: rem(4);
    overflow: hidden;

    thead {
      background: #3d4d64;
    }
    tbody {
      background: rgba(#3d4d64, 0.6);
      tr:not(:last-child) {
        border-bottom: thin solid rgba(#000000, 0.2) !important;
      }
      tr {
        td {
          &:first-child {
            color: rgba(#fff, 0.38);
          }
        }
      }
    }
  }
}
</style>
